<template>
  <el-container>
    <!-- <div v-wechat-title="$route.meta.title">门诊缴费</div> -->
    <!-- <el-header></el-header> -->
    <el-main>
      <!--  -->
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>基本信息</span>
        </div>
      </div>
      <!--  -->

      <el-card class="box-card">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">就诊人</div>
              <div class="grid-content bg-purple">单据号</div>
              <div class="grid-content bg-purple">开单科室</div>
              <div class="grid-content bg-purple">开单人</div>
              <div class="grid-content bg-purple">开单日期</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div class="grid-content bg-purple-light">
                <span>{{name}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{ChargeListsOne.SerialNumber}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{ChargeListsOne.Dept}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{ChargeListsOne.DocName}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>{{ChargeListsOne.ChargeDate}}</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>费用明细</span>
        </div>
      </div>
      <!--  -->
      <el-card class="box-card payz">
        <el-table :data="ChargeListsTwo" style="font-size: 13px;color:#333;">
          <el-table-column prop="ChargeName" label="项目名称" width="111px"></el-table-column>
          <el-table-column prop="Amount" label="单价(元)"></el-table-column>
          <el-table-column prop="PrescriptionNo" label="数量.单位"></el-table-column>
          <el-table-column prop="Amount" label="金额(元)"></el-table-column>
        </el-table>
      </el-card>
    </el-main>
    <!--  -->
    <el-footer class="outfooter">
      <div>
        <el-row>
          <el-col :span="8">
            <div class="grid-content bg-purple" style="color:#666;font-size: 17px;">总缴费金额:</div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <span style="color:#FF7800;font-size: 17px;">￥186.00</span>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <router-link tag="a" to="/shopcart/Outpatient/outpatientRecord">
                <el-button type="primary">确定支付</el-button>
              </router-link>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-footer>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<style>
.payz .el-card__body {
  padding: 0px;
}
</style>
<script>
import axios from "axios";
import * as commonApi from "../../api/commonfetch";
export default {
  data() {
    return {
      ChargeListsOne: [],
      ChargeListsTwo: [],
      id: this.$route.params.id,
      name: this.$route.params.name
    };
  },
  mounted() {
    axios
      .get("http://lc-7fHWOBLD.cn-n1.lcfile.com/7a003906730f9b104c50/json.json")
      .then(response => {
        let rea = response.data.data;
        rea.forEach(e => {
          if (true) {
            e.ChargeDate = new Date(e.ChargeDate)
              .toJSON()
              .replace(/T/g, " ")
              .replace(/\.[\d]{3}Z/, "");
          }
        });
        this.ChargeListsOne = rea[this.id];
        this.ChargeListsTwo = rea[this.id].ChargeLists;
        console.log(this.ChargeListsTwo);

      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {}
};
</script>



